<template>
  <div>
    <div class="head">随机分配n份金额</div>
    <mt-field
      label="金额"
      placeholder="请输入金额"
      type="number"
      v-model="total"
    ></mt-field>
    <mt-field
      label="份数"
      placeholder="请输入份数"
      type="number"
      v-model="number"
    ></mt-field>
    <div class="balnk">
      温馨提示：单份金额不得低于0.01元
    </div>
    <mt-button type="primary" size="large" @click="getRandomNum"
      >请分配</mt-button
    >
    <div>
      <mt-badge
        size="normal"
        v-for="(item, index) in arr"
        :key="index"
        :type="type()"
        >{{ item }}</mt-badge
      >
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";

export default {
  data() {
    return {
      total: 200,
      number: 10,
      arr: [],
      flag: true,
    };
  },
  methods: {
    getRandomNum() {
      let _that = this;
      this.arr = [];

      const Math_e = Math.E;
      const Math_Pi = Math.PI;
      const minMoney = 0.01; //最小红包金额

      Number.prototype.fixed = function(n) {
        if (n > 0) return parseFloat(this.toFixed(n));
        else return parseInt(this);
      };

      main(this.total, this.number);

      function main(Money, num) {
        if (/^((-\d+)|(0+))$/.test(Money)||Money=="") {
          Toast({
            message: "请输入正确金额",
            duration: 2000,
          });
          return;
        } else if (!/^\d+$/.test(num)||num==0) {
          Toast({
            message: "请输入正确份数",
            duration: 2000,
          });
          return;
        } else if (num == 1) {
          _that.arr.push(Money);
          return;
        }

        const realMoney = (Money - num * minMoney).fixed(2); //可分配的红包金额
        if (realMoney < 0) return null;
        let currentNum = num;
        let currentMoney = realMoney;
        console.log(`红包总金额${Money}`);
        console.log(`红包瓜分人数${num}`);

        while (currentNum != 0) {
          let nowMoney;
          let { h, r } = compute(currentNum, currentMoney);
          if (currentNum === 1) {
            nowMoney = currentMoney + 0.01;
          } else {
            nowMoney = getNum(0, currentMoney, h, r).fixed(2) + minMoney;
          }
          nowMoney = nowMoney.fixed(2);
          console.log(`第${num - currentNum + 1}个人红包为${nowMoney}`);
          _that.arr.push(nowMoney);

          currentNum--;
          currentMoney -= (nowMoney - minMoney).fixed(2);

          console.log(
            `剩余红包为${(currentMoney + minMoney * currentNum).fixed(2)}`
          );
        }
      }

      //计算期望和方差
      function compute(num, money) {
        const h = money / num; //期望金额
        let r = money / (num * 10); //方差
        r = r < 0.01 ? 0.01 : r;
        return {
          r: r,
          h: h.fixed(2),
        };
      }

      //正态分布表达式
      function NormalDistribution(h, r, x) {
        const base = (1 / Math.log2(2 * Math_Pi)) * Math_e;
        const expon = -((x - h) ** 2) / (2 * r ** 2);
        return base ** expon;
      }

      //生成从minNum到maxNum的随机数
      function randomNum(min, max) {
        return Math.random() * (max - min) + min;
      }

      //根据正态分布随机生成一个数
      function getNum(min, max, h, r) {
        if (typeof min !== `number` || typeof max !== `number` || min > max)
          throw new Error(`数据异常`);
        if (min === max) return min;
        while (_that.flag) {
          const x = randomNum(min, max);
          const y = Math.random();
          if (NormalDistribution(h, r, x) > y) {
            return x;
          }
        }
      }
    },
    // getRandomNum() {
    //   let range = this.total,
    //     res = [];

    //   for (let i = 0; i < this.number - 1; i++) {
    //     var item = Math.ceil(Math.random() * (range / 2));
    //     res.push(item);
    //     range -= item;
    //   }

    //   res.push(range);

    //   this.arr = res;
    // },
    type() {
      let typeColor = ["primary", "error", "success", "warning"];
      return typeColor[Math.floor(Math.random() * 4)];
    },
  },
};
</script>

<style>
.head {
  background-color: #fafafa;
  height: 1rem;
  line-height: 1rem;
  font-size: 0.36rem;
}
.balnk {
  height: 1rem;
  line-height: 1rem;
  font-size: 0.2rem;
  color: red;
}
</style>
